<template>
  <div class="maxset">
    <div class="card" v-for="item in getMenu" :key="item.id">
      <img
        data-toggle="modal"
        data-target="#exampleModal"
        :src="'./static/cakeimg/' + item.id + '.jpg'"
        class="card-img-top"
        @click="getId(item)"
      />
      <div class="card-body">
        <h5 class="card-title">{{ item.name }}</h5>
        <p class="card-text">{{ item.mark }}</p>
        <p class="card-size">售價：{{ item.size }}</p>
      </div>
    </div>
    <Sweetmodal ref="clear" :gettomodal="userclick"></Sweetmodal>
  </div>
</template>

<script>
import Sweetmodal from "./sweetmodal.vue";
import Buttons from "./buttons.vue";
export default {
  name: "Menulist",
  data() {
    return {
      userclick: {
     
      },
    };
  },
  computed: {
    getMenu() {
      return this.$store.state.menudata.sweet;
    },
  },
  methods: {
    getId(item) {
      this.userclick = item;
      this.$refs.clear.clearchoose();
    },
  },
  components: {
    Sweetmodal,
  },
};
</script>

<style lang="scss" scoped>
.maxset {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  max-width: 900px;
  width: 100%;
  margin: auto;
  max-height: 80vh;
  overflow-y: auto;
  padding-top: 70px;
  -ms-overflow-style: none;
  &::-webkit-scrollbar {
    display: none;
  }
  .card {
    width: 30%;
    margin: 10px;
    .card-title{
      font-weight: 500;
      font-size: 20px;
    }
    .card-text{
      min-height: 120px;
      letter-spacing: 1px;

      overflow:hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 5;
      -webkit-box-orient: vertical;
      white-space: normal;
    }
    .card-size{
      font-size: 20px;
    }
  }
}

@media (max-width: "776px") {
  .maxset {
    .card {
      flex-direction: unset;
      width: 100%;
      height: 180px;
      margin: 10px 0px;
      img {
        width: 180px;
      }
    }
  }
}
</style>


