<template>
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel">{{ objinfo.name }}</h5>
          <button
            type="button"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <div class="imgsize">
            <img :src="'../../static/cakeimg/' + objinfo.id + '.jpg'" alt="" />
          </div>
          <div class="fontsize">
            <p>{{ objinfo.mark }}</p>
          </div>
        </div>

        <div class="modal-footer">
          <span class="mr-3">售價：{{ objinfo.size }}</span>
          <span>數量：</span>
          <div class="mr-3" role="group" aria-label="Basic example">
            <button
              type="button"
              class="btn btn-danger"
              @click="count--"
              :disabled="boola"
            >
              -
            </button>
            <button type="button" class="btn text-dark" disabled>
              {{ count }}
            </button>
            <button
              type="button"
              class="btn btn-success"
              @click="count++"
              :disabled="boolb"
            >
              +
            </button>
          </div>
          <button type="button" class="btn btn-primary" @click="joinshop">
            加入購物車
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Sweetmodal",
  data() {
    return {
      count: 1,
    };
  },
  props: {
    gettomodal: {
      Type: Object,
      default() {
        return {
          id: 0,
          name: "蛋糕",
          mark: "測試",
          size: 10,
        };
      },
    },
  },
  methods: {
    joinshop() {
      this.$store.commit("pushshopcar", {
        name: this.objinfo.name,
        size: "一般",
        ice: "---",
        sweet: "---",
        count: this.count,
        price: this.totalprice,
      });
    },
    clearchoose() {
      this.count = 1;
    },
  },
  computed: {
    objinfo() {
      return this.gettomodal;
    },
    boola() {
      return this.count <= 1 ? true : false;
    },
    boolb() {
      return this.count >= 5 ? true : false;
    },
    totalprice() {
      return this.count * this.objinfo.size;
    },
    joinbtnctl() {},
    shopjsonlength() {
      return this.$store.state.shopcar.length;
    },
  },
  watch: {},
};
</script>

<style lang="scss" scoped>
.modal-body {
  display: flex;
  .imgsize {
    img {
      width: 160px;
      margin-right: 15px;
    }
  }
  .fontsize {
    p {
      letter-spacing: 3px;
      line-height: 1.5;
    }
  }
}
.modal-footer {
  span {
    font-size: 20px;
  }
}
</style>


